import { Injectable } from '@angular/core';

import { HttpClient } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { Constant } from './../../shared/constant/constant';
import { Employee } from './../../shared/model/employee';

@Injectable()
export class EmployeeService {

  constructor(private http: HttpClient) { }

  errorHandler(error: any) {
    console.log(error);
    return throwError(error);
  }

  getAllEmployees(page, size, sort): Observable<any> {
    return this.http.get<Employee[]>(Constant.API_ENDPOINT + '/employee' ,
      {
        params: {
          page: page,
          size: size,
          sort: sort
        }
      })
      .pipe(catchError(this.errorHandler));
  }
  
  getCurrentEmployee(): Observable<Employee> {
    return this.http.get<Employee>(Constant.API_ENDPOINT + '/employee/me')
      .pipe(catchError(this.errorHandler));
  }

  getEmployeeById(id): Observable<Employee[]> {
    return this.http.get<Employee[]>(Constant.API_ENDPOINT + '/employee/' + id)
      .pipe(catchError(this.errorHandler));
  }

  getAllAdminEmployee(): Observable<Employee[]> {
    return this.http.get<Employee[]>(Constant.API_ENDPOINT + '/admin')
      .pipe(catchError(this.errorHandler));
  }

  createEmployee(EmployeeData): Observable<Employee[]> {
    return this.http.post<any>(Constant.API_ENDPOINT + '/createEmployee', EmployeeData)
      .pipe(catchError(this.errorHandler));

  }

  updateEmployee(EmployeeData): Observable<Employee[]> {
    return this.http.put<any>(Constant.API_ENDPOINT + '/updateEmployee', EmployeeData)
      .pipe(catchError(this.errorHandler));
  }


}
