import { Component } from '@angular/core';
import { AuthService } from './../../service/authService/auth.service';
import { SidebarService } from './../../service/sidebarService/sidebar.service';
import { EmployeeService } from './../../service/httpService/employee.service';
import{Employee} from './../../shared/model/employee';

@Component({
  selector: 'app-top-bar',
  templateUrl: './top-bar.component.html',
  styleUrls: ['./top-bar.component.css']
})
export class TopBarComponent {

  isLoggedIn: boolean = this._auth.isLoggedIn();

  selected_user_msg;
  user: Employee;

  constructor(private _sideBarService: SidebarService, public _auth: AuthService, private _employeeService: EmployeeService) { }

  ngOnInit() {
    this.user = new Employee();
    this.getUserInfo();
  }

  getUserInfo() {
    this._employeeService.getCurrentEmployee()
      .subscribe(
        data => {
          this.user = data;
        },
        error => {
          this.selected_user_msg = 'Oops ! Can\'t load Profile';
        });
  }

  toggleSidebar() {
    this._sideBarService.toggle();
  }

}
