import { Component, OnInit } from '@angular/core';
import { SidebarService } from './../../service/sidebarService/sidebar.service';
import { AuthService } from './../../service/authService/auth.service';

@Component({
  selector: 'app-side-bar',
  templateUrl: './side-bar.component.html',
  styleUrls: ['./side-bar.component.css']
})
export class SidebarComponent implements OnInit {

  isOpen = false;

  constructor(private _sidebarService: SidebarService, public _authService: AuthService) { }

  ngOnInit() {
    this._sidebarService.change.subscribe(isOpen => {
      //console.log("called ths")
      this.isOpen = isOpen;
    });
  }

}
